import { TextField } from "@mui/material";
import "./main.css";

function TextBox(props) {
  const { id, name, label, rows, placeholder, onChange, value } = props;
  return (
    <TextField
      id={id}
      name={name}
      label={label}
      multiline
      rows={rows}
      style={{ padding: 10, minWidth: `calc(100%)`}}
      placeholder={placeholder}
      // variant="filled"
      onChange={onChange}
      value={value}
    />
  );
}
TextBox.defaultProps = {
  // rows:10,
}
export default TextBox;
