import {ArrowDownwardOutlined} from "@mui/icons-material";
import {Button, ButtonGroup, Container, Grid, Typography} from "@mui/material";
import CryptoJS from "crypto-js";
import * as React from "react";
import "../main.css";
import TextBox from "../TextBox";
import Main from "../Main";

function HashGenerator() {
    const postName = 'hash.md'
    const [post, setPost] = React.useState("");
    React.useEffect(() => {
        import(`../markdown/${postName}`)
            .then((res) => {
                fetch(res.default)
                    .then((res) => res.text())
                    .then((res) => setPost(res));
            })
            .catch((err) => console.log(err));

    });
    const [data, setData] = React.useState({
        plainText: "",
        hashedMd5: "",
        hashedSha1: "",
        hashedSha224: "",
        hashedSha256: "",
        hashedSha3: "",
        hashedSha384: "",
        hashedSha512: "",
    });
    const {
        plainText,
        hashedMd5,
        hashedSha1,
        hashedSha224,
        hashedSha256,
        hashedSha3,
        hashedSha384,
        hashedSha512,
    } = data;

    const hashString = (e) => {
        const {value, name} = e.target;
        const plainText = value;
        console.log("Encoding..." + value);
        setData({
            ...data,
            [name]: value,
            ["hashedMd5"]: CryptoJS.MD5(plainText),
            ["hashedSha1"]: CryptoJS.SHA1(plainText),
            ["hashedSha224"]: CryptoJS.SHA224(plainText),
            ["hashedSha256"]: CryptoJS.SHA256(plainText),
            ["hashedSha3"]: CryptoJS.SHA3(plainText),
            ["hashedSha384"]: CryptoJS.SHA384(plainText),
            ["hashedSha512"]: CryptoJS.SHA512(plainText),
        });
        console.log("Encodeing Done! ");
        console.log(data);
    };

    return (
        <Container justify="center">
            <div className="App">
                <Typography variant="h2">Hash Generator</Typography>
                <TextBox
                    id="textForDecoding"
                    name="plainText"
                    label="Text for plainText"
                    value={plainText}
                    rows={10}
                    onChange={hashString}
                />
                <ButtonGroup variant="contained">
                    <Button startIcon={<ArrowDownwardOutlined/>}>Encode</Button>
                </ButtonGroup>
                <TextBox
                    id="hashedMd5"
                    name="hashedMd5"
                    label="Text for MD5"
                    value={hashedMd5}
                />
                <TextBox id="hashedSha1" label="Text for SHA1" value={hashedSha1}/>
                <TextBox
                    id="hashedSha224"
                    label="Text for SHA224"
                    value={hashedSha224}
                />
                <TextBox
                    id="hashedSha256"
                    label="Text for SHA256"
                    value={hashedSha256}
                />
                <TextBox id="hashedSha3" label="Text for SHA3" value={hashedSha3}/>
                <TextBox
                    id="hashedSha384"
                    label="Text for SHA384"
                    value={hashedSha384}
                />
                <TextBox
                    id="hashedSha512"
                    label="Text for SHA512"
                    value={hashedSha512}
                />
                <Grid container justifyContent={"center"} spacing={1} sx={{mt: 3}}>
                    <Main title="Base64 Encode and Decode Tool" post={post}/>
                </Grid>
            </div>
        </Container>
    );
}

export default HashGenerator;
