import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import PropTypes from "prop-types";
import * as React from "react";
import { Route, Routes } from "react-router-dom";
import Endecoder from "./Encoder/Endecoder";
import NavBar from "./NavBar";
import UrlEndecoder from "./Encoder/UrlEndecoder";
import HashGenerator from "./Encoder/HashGenerator";
import HMACGenerator from "./Encoder/HMACGenerator";
import JWTEndecoder from "./Encoder/JWTEndecoder";
import RSAGenerator from "./Encoder/RSAGenerator";
const drawerWidth = 240;

function ResponsiveDrawer(props) {
  return (
    <Box sx={{ display: "flex" }}>
      {/* ============NAVBAR============= */}
      <NavBar />

      {/* ============MAIN============= */}
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
          width: { sm: `calc(100% - ${drawerWidth}px)` },
        }}
      >
        <Routes>
          <Route path="/" element={<UrlEndecoder />} />
          <Route path="base" element={<Endecoder />} />
          <Route path="hash" element={<HashGenerator />} />
          <Route path="hmac" element={<HMACGenerator />} />
          <Route path="jwt" element={<JWTEndecoder />} />
          <Route path="rsa" element={<RSAGenerator />} />
        </Routes>

        <Toolbar />
      </Box>
    </Box>
  );
}

ResponsiveDrawer.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

export default ResponsiveDrawer;
