import {
  ArrowDownwardOutlined,
  ArrowUpwardOutlined,
} from "@mui/icons-material";
import {Button, ButtonGroup, Container, Grid, Typography} from "@mui/material";
import * as React from "react";
import "../main.css";
import TextBox from "../TextBox";
import Main from "../Main";

function UrlEndecoder() {
  const postName = 'urlencode.md'
  const [post, setPost] = React.useState("");
  React.useEffect(() => {
    import(`../markdown/${postName}`)
        .then((res) => {
          fetch(res.default)
              .then((res) => res.text())
              .then((res) => setPost(res));
        })
        .catch((err) => console.log(err));

  });

  const [data, setData] = React.useState({
    encoding: "",
    decoding: "",
  });
  const { encoding, decoding } = data;

  const onChange = (e) => {
    const { value, name } = e.target;
    console.log(value + " " + name);
    setData({
      ...data,
      [name]: value,
    });
  };

  const urlEncode = (e) => {
    console.log("Encoding...");
    const val = encodeURIComponent(`${decoding}`);
    console.log({ val });
    setData({ ...data, ["encoding"]: `${val}` });
  };

  const urlDecode = () => {
    console.log("Decoding...");
    const val = decodeURIComponent(`${encoding}`);
    console.log({ val });
    setData({ ...data, ["decoding"]: `${val}` });
  };

  return (
    <Container justify="center">
      <div className="App">
        <Typography variant="h2">URLEncoder / Decoder</Typography>
        <TextBox
          id="textForDecoding"
          label="Text for decoding"
          name="decoding"
          value={decoding}
          onChange={onChange}
        />
        <ButtonGroup variant="contained">
          <Button startIcon={<ArrowDownwardOutlined />} onClick={urlEncode}>
            Encode
          </Button>

          <Button startIcon={<ArrowUpwardOutlined />} onClick={urlDecode}>
            Decode
          </Button>
        </ButtonGroup>

        <TextBox
          id="textForEncoding"
          label="Text for encoding"
          name="encoding"
          value={encoding}
          onChange={onChange}
        />
        <Grid container justifyContent={"center"} spacing={1} sx={{ mt: 3 }}>
          <Main title="URL Encode and Decode Tool" post={post} />
        </Grid>
      </div>
    </Container>
  );
}

export default UrlEndecoder;
