import {
  ArrowDownwardOutlined,
  ArrowUpwardOutlined,
} from "@mui/icons-material";
import {
  Button,
  ButtonGroup,
  Card,
  CardContent,
  Checkbox,
  Container,
  FormControl,
  FormControlLabel,
  FormGroup,
  InputLabel,
  MenuItem,
  Select,
  Typography,
} from "@mui/material";
import CryptoJS from "crypto-js";
import * as React from "react";
import "../main.css";
import TextBox from "../TextBox";

function JWTEndecoder() {
  const API_URL = "https://h65se5.deta.dev";
  // const API_URL = "http://0.0.0.0:8000";
  const [data, setData] = React.useState({
    encoded: false,
    encodedText: "",
    secretForDecoding: "",
    header: "",
    payload: "",
    secret: "",
    algorithm: "HS256",
    decodedVerified: "",
    verified: "",
  });
  const {
    encoded,
    encodedText,
    secretForDecoding,
    header,
    payload,
    secret,
    algorithm,
    decodedVerified,
    verified,
  } = data;

  const onChange = (e) => {
    const { value, name } = e.target;
    console.log(value + " " + name);
    setData({
      ...data,
      [name]: value,
    });
  };

  const onChecked = (e) => {
    const { value, name } = e.target;
    console.log(e.target.checked);
    setData({ ...data, [name]: e.target.checked });
  };

  const handleChange = (event) => {
    setData({ ...data, ["algorithm"]: event.target.value });
  };

  const jwtEncode = (e) => {
    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");

    var secretPayload = secret;
    if (encoded) {
      secretPayload = CryptoJS.enc.Utf8.stringify(
        CryptoJS.enc.Base64url.parse(secret)
      );
    }

    console.log("data: " + header + payload);
    var raw = JSON.stringify({
      header: JSON.parse(header),
      payload: JSON.parse(payload),
      algorithm: algorithm,
      secret: secretPayload,
    });

    var requestOptions = {
      method: "POST",
      mode: "cors",
      headers: myHeaders,
      body: raw,
      redirect: "follow",
    };

    fetch(API_URL + "/jwt:encode", requestOptions)
      .then((response) => response.json())
      .then((result) => {
        setData({
          ...data,
          // ["header"]: JSON.stringify(result.header, null, 4),
          ["encodedText"]: result.encoded,
        });
      })
      .catch((error) => console.log(error));
  };

  ///  === DECODE === ///
  const jwtDecode = (e) => {
    if (!encodedText) {
      alert("Encoded text is empty!");
    }
    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    myHeaders.append("Access-Control-Allow-Origin", "*");

    var secretPayload = secretForDecoding;
    if (encoded) {
      secretPayload = CryptoJS.enc.Utf8.stringify(
        CryptoJS.enc.Base64url.parse(secretForDecoding)
      );
    }
    var raw = JSON.stringify({
      encoded: encodedText,
      algorithm: algorithm,
      secret: secretPayload,
    });

    var requestOptions = {
      method: "POST",
      headers: myHeaders,
      body: raw,
      mode: "cors",
      redirect: "follow",
    };

    fetch(API_URL + "/jwt:decode", requestOptions)
      .then((response) => response.json())
      .then((result) => {
        setData({
          ...data,
          ["header"]: JSON.stringify(result.header, null, 4),
          ["payload"]: JSON.stringify(result.payload, null, 4),
          ["secret"]: result.secret,
          ["decodedVerified"]: result.verified,
        });
      })
      .catch((error) => console.log(error));
    console.log(data);
  };

  return (
    <Container justify="center">
      <div className="App">
        <Typography variant="h2">JWT Encoder / Decoder</Typography>
        <TextBox
          id="textForDecoding"
          label="Encoded Text"
          name="encodedText"
          value={encodedText}
          rows={12}
          onChange={onChange}
        />
        <TextBox
          id="secretForDecoding"
          label="Secret for Decoding"
          name="secretForDecoding"
          value={secretForDecoding}
          rows={4}
          onChange={onChange}
        />

        <FormControl variant="filled" sx={{ minWidth: 120, padding: 1 }}>
          <InputLabel id="algorithm-label">Algorithm</InputLabel>
          <Select
            variant="outlined"
            labelId="algorithm-label"
            id="algorithm"
            value={algorithm}
            name="algorithm"
            label="Algorithm"
            onChange={handleChange}
            displayEmpty
          >
            <MenuItem value={"HS256"}>HS256</MenuItem>
            <MenuItem value={"HS384"}>HS384</MenuItem>
            <MenuItem value={"HS512"}>HS512</MenuItem>

            <MenuItem value={"RS256"}>RS256</MenuItem>
            <MenuItem value={"RS384"}>RS384</MenuItem>
            <MenuItem value={"RS512"}>RS512</MenuItem>

            <MenuItem value={"ES256"}>ES256</MenuItem>
            <MenuItem value={"ES384"}>ES384</MenuItem>
            <MenuItem value={"ES512"}>ES512</MenuItem>

            <MenuItem value={"PS256"}>PS256</MenuItem>
            <MenuItem value={"PS384"}>PS384</MenuItem>
            <MenuItem value={"PS512"}>PS512</MenuItem>
          </Select>
        </FormControl>
        <ButtonGroup variant="contained">
          <Button startIcon={<ArrowUpwardOutlined />} onClick={jwtEncode}>
            Encode
          </Button>

          <Button startIcon={<ArrowDownwardOutlined />} onClick={jwtDecode}>
            Decode
          </Button>
        </ButtonGroup>
        <br />
        {/* <TextBox
          id="verified"
          label="Decode Result"
          name="verified"
          value={verified}
          rows={2}
        /> */}
        <TextBox
          id="header"
          label="HEADER"
          name="header"
          value={header}
          onChange={onChange}
          rows={4}
        />
        <TextBox
          id="payload"
          label="PAYLOAD"
          name="payload"
          value={payload}
          onChange={onChange}
          rows={4}
        />
        <TextBox
          id="secret"
          label="SECRET"
          name="secret"
          value={secret}
          onChange={onChange}
          rows={4}
        />
        <FormGroup>
          <FormControlLabel
            control={
              <Checkbox
                onChange={onChecked}
                id="encoded"
                name="encoded"
                checked={encoded[0] && encoded[1]}
              />
            }
            label="Is secret base64 encoded?"
          />
        </FormGroup>
        <Card sx={{ minWidth: `calc(100%)` }}>
          <CardContent>
            <Typography
              sx={{ fontSize: 14 }}
              color="text.secondary"
              gutterBottom
            >
              VERIFY SIGNATURE
            </Typography>
            <Typography variant="body1" component="div" sx={{ paddingLeft: 1 }}>
              HASHING(
            </Typography>
            <Typography variant="body2" sx={{ paddingLeft: 3 }}>
              base64UrlEncode(header) + "." +
              <br />
              base64UrlEncode(payload),
              <br />
              secret
            </Typography>
            <Typography variant="body1" component="div" sx={{ paddingLeft: 1 }}>
              )
            </Typography>
          </CardContent>
        </Card>
        {/* </div> */}
      </div>
    </Container>
  );
}
export default JWTEndecoder;
