import {
    ArrowDownwardOutlined,
    ArrowUpwardOutlined,
} from "@mui/icons-material";
import {
    Button,
    ButtonGroup,
    Container,
    FormControl,
    Grid,
    InputLabel,
    MenuItem,
    Select,
    Typography,
} from "@mui/material";
import * as React from "react";
import "../main.css";
import TextBox from "../TextBox";
import Divider from "@mui/material/Divider";
import Chip from "@mui/material/Chip";
import {trackPromise} from "react-promise-tracker";
import {usePromiseTracker} from "react-promise-tracker";
import PulseLoader from "react-spinners/PulseLoader";

function RSAGenerator() {

    const API_URL = "https://h65se5.deta.dev";
    // const API_URL = "http://0.0.0.0:8000";
    const [data, setData] = React.useState({
        keySize: 2048,
        privateKey: "",
        publicKey: "",
        plainText: "",
        cipherText: "",
    });
    const {keySize, privateKey, publicKey, plainText, cipherText} = data;
    const {promiseInProgress} = usePromiseTracker();

    const handleChange = (event) => {
        setData({...data, ["keySize"]: event.target.value});
    };

    const onChange = (e) => {
        const {value, name} = e.target;
        console.log(value + " " + name);
        setData({
            ...data,
            [name]: value,
        });
    };

    const onClick = () => {
        var requestOptions = {
            method: "GET",
        };
        trackPromise(
            fetch(API_URL + `/rsa?keysize=${keySize}`, requestOptions)
                .then((response) => response.json())
                .then((result) => {
                    setData({
                        ...data,
                        ["privateKey"]: result.private_key,
                        ["publicKey"]: result.public_key,
                    });
                })
                .catch((error) => console.log(error))
        );
    };

    const encryptText = () => {
        if (!publicKey) {
            alert("Public key is empty");
        }
        var myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");

        var payload = JSON.stringify({
            plain_text: plainText,
            public_key: publicKey,
        });

        var requestOptions = {
            method: "POST",
            headers: myHeaders,
            body: payload,
            redirect: "follow",
        };

        fetch(API_URL + `/rsa:encrypt`, requestOptions)
            .then((response) => response.json())
            .then((result) => {
                setData({
                    ...data,
                    ["cipherText"]: result.cipher_text,
                });
            })
            .catch((error) => console.log(error));
    };

    const decryptText = () => {
        if (!privateKey) {
            alert("Private key is empty");
        }
        var myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");

        var payload = JSON.stringify({
            cipher_text: cipherText,
            private_key: privateKey,
        });

        var requestOptions = {
            method: "POST",
            headers: myHeaders,
            body: payload,
            redirect: "follow",
        };

        fetch(API_URL + `/rsa:decrypt`, requestOptions)
            .then((response) => response.json())
            .then((result) => {
                setData({
                    ...data,
                    ["plainText"]: result.plain_text,
                });
            })
            .catch((error) => console.log(error));
    };

    return (
        <Container justify="center">
            <div className="App">
                <Typography variant="h2">RSA Generator</Typography>
                <FormControl variant="filled" sx={{minWidth: 120, padding: 1}}>
                    <InputLabel id="key-size-label">Key Size</InputLabel>
                    <Select
                        variant="outlined"
                        labelId="key-size-label"
                        id="keySize"
                        value={keySize}
                        name="keySize"
                        label="keySize"
                        onChange={handleChange}
                    >
                        <MenuItem value={"512"}>512</MenuItem>
                        <MenuItem value={"1024"}>1024</MenuItem>
                        <MenuItem value={"2048"}>2048</MenuItem>
                        <MenuItem value={"4096"}>4096</MenuItem>
                    </Select>
                </FormControl>
                <ButtonGroup variant="contained">
                    <Button startIcon={<ArrowDownwardOutlined/>} onClick={onClick}>
                        Generate
                    </Button>
                </ButtonGroup>

                <div>
                    {promiseInProgress === true ? (
                        <div>
                            <br/>
                            <br/>
                            <PulseLoader color="#57544e"/>{" "}
                        </div>
                    ) : null}
                </div>

                <TextBox id="publicKey" label="Public Key" value={publicKey}/>
                <TextBox id="privateKey" label="Private Key" value={privateKey}/>
                <br/>
                <Divider style={{width: "100%"}}>
                    <Chip label="RSA Encryption Test"/>
                </Divider>

                {/* EncryptTest */}
                <TextBox
                    id="plainText"
                    name="plainText"
                    label="Plain Text"
                    value={plainText}
                    onChange={onChange}
                    rows={2}
                />
                <ButtonGroup variant="contained">
                    <Button startIcon={<ArrowDownwardOutlined/>} onClick={encryptText}>
                        Encrypt
                    </Button>
                    <Button startIcon={<ArrowUpwardOutlined/>} onClick={decryptText}>
                        Decrypt
                    </Button>
                </ButtonGroup>
                <TextBox
                    id="cipherText"
                    name="cipherText"
                    label="Cipher Text"
                    value={cipherText}
                    rows={2}
                />

            </div>
        </Container>
    );
}

export default RSAGenerator;
