import { ArrowDownwardOutlined } from "@mui/icons-material";
import {Button, ButtonGroup, Container, Grid, Typography} from "@mui/material";
import CryptoJS from "crypto-js";
import Base64 from "crypto-js/enc-base64";
import Hex from "crypto-js/enc-hex";
import Latin1 from "crypto-js/enc-latin1";
import Utf8 from "crypto-js/enc-utf8";
import * as React from "react";
import "../main.css";
import TextBox from "../TextBox";
import Main from "../Main";

function toString(func, str) {
  try {
    return func.stringify(str);
  } catch (error) {
    console.log(error);
    return "";
  }
}
function toHex(str) {
  try {
    return Hex.parse(str);
  } catch (error) {
    console.log(error);
    return "";
  }
}
function Endecoder() {
  const postName = 'base64encode.md'
  const [post, setPost] = React.useState("");
  React.useEffect(() => {
    import(`../markdown/${postName}`)
        .then((res) => {
          fetch(res.default)
              .then((res) => res.text())
              .then((res) => setPost(res));
        })
        .catch((err) => console.log(err));

  });

  const [data, setData] = React.useState({
    plainText: "",
    base64: "",
    latin1: "",
    hex: "",
    utf8: "",
    utf16: "",
    utf16be: "",
    utf16le: "",
  });
  const { plainText, base64, latin1, hex, utf8, utf16, utf16be, utf16le } =
    data;

  const onChange = (e) => {
    const { value, name } = e.target;
    console.log(value + " " + name);
    setData({
      ...data,
      [name]: value,
    });
  };

  const strignEncode = (e) => {
    console.log(`Encoding...${plainText}`);

    const byte = Utf8.parse(plainText);
    setData({
      ...data,
      ["base64"]: Base64.stringify(byte),
      ["latin1"]: Latin1.stringify(byte),
      ["hex"]: Hex.stringify(byte),
      ["utf8"]: Utf8.parse(plainText),
      ["utf16"]: CryptoJS.enc.Utf16.parse(plainText),
      ["utf16be"]: CryptoJS.enc.Utf16BE.parse(plainText),
      ["utf16le"]: CryptoJS.enc.Utf16LE.parse(plainText),
    });
  };
  const strignDecode = (e) => {
    console.log(`Decoding...${plainText}`);

    setData({
      ...data,
      ["base64"]: toString(Utf8, Base64.parse(plainText)),
      ["latin1"]: toString(Utf8, Latin1.parse(plainText)),
      ["hex"]: toString(Utf8, Hex.parse(plainText)),
      ["utf8"]: toString(Utf8, toHex(plainText)),
      ["utf16"]: toString(CryptoJS.enc.Utf16, toHex(plainText)),
      ["utf16be"]: toString(CryptoJS.enc.Utf16BE, toHex(plainText)),
      ["utf16le"]: toString(CryptoJS.enc.Utf16LE, toHex(plainText)),
    });
  };

  return (
    <Container justify="center">
      <div className="App">
        <Typography variant="h2">Encoder / Decoder</Typography>
        <TextBox
          id=""
          label="Text for encoding/decoding"
          name="plainText"
          value={plainText}
          onChange={onChange}
        />
        <ButtonGroup variant="contained">
          <Button startIcon={<ArrowDownwardOutlined />} onClick={strignEncode}>
            Encode
          </Button>

          <Button startIcon={<ArrowDownwardOutlined />} onClick={strignDecode}>
            Decode
          </Button>
        </ButtonGroup>

        <TextBox id="base64" label="Base64" name="base64" value={base64} />
        <TextBox id="latin1" label="Latin-1" name="latin1" value={latin1} />
        <TextBox id="hex" label="Hex" name="hex" value={hex} />
        <TextBox id="utf8" label="UTF-8" name="utf8" value={utf8} />
        <TextBox id="utf16" label="UTF-16" name="utf16" value={utf16} />
        <TextBox id="utf16be" label="UTF-16BE" name="utf16be" value={utf16be} />
        <TextBox id="utf16le" label="UTF-16LE" name="utf16le" value={utf16le} />
        <Grid container justifyContent={"center"} spacing={1} sx={{ mt: 3 }}>
          <Main title="Base64 Encode and Decode Tool" post={post} />
        </Grid>
      </div>
    </Container>
  );
}

export default Endecoder;
