import {
  ArrowDownwardOutlined
} from "@mui/icons-material";
import { Button, ButtonGroup, Container, Typography } from "@mui/material";
import CryptoJS from "crypto-js";
import * as React from "react";
import "../main.css";
import TextBox from "../TextBox";

function HMACGenerator() {
  const [data, setData] = React.useState({
    plainText: "",
    secret: "",
    hashedMd5: "",
    hashedSha1: "",
    hashedSha224: "",
    hashedSha256: "",
    hashedSha3: "",
    hashedSha384: "",
    hashedSha512: "",
  });
  const {
  plainText,
    secret,
    hashedMd5,
    hashedSha1,
    hashedSha224,
    hashedSha256,
    hashedSha3,
    hashedSha384,
    hashedSha512,
  } = data;

  const onChange = (e) => {
    const { value, name } = e.target;
    console.log(value + " " + name);
    setData({
      ...data,
      [name]: value,
    });
  };

  const hashString = (e) => {
    console.log(`Encoding...${plainText}`);
    setData({
      ...data,
      ["hashedMd5"]: CryptoJS.HmacMD5(plainText, secret),
      ["hashedSha1"]: CryptoJS.HmacSHA1(plainText, secret),
      ["hashedSha224"]: CryptoJS.HmacSHA224(plainText, secret,),
      ["hashedSha256"]: CryptoJS.HmacSHA256(plainText, secret),
      ["hashedSha3"]: CryptoJS.HmacSHA3(plainText, secret),
      ["hashedSha384"]: CryptoJS.HmacSHA384(plainText, secret),
      ["hashedSha512"]: CryptoJS.HmacSHA512(plainText, secret),
    });
    console.log("Encodeing Done! ");
    console.log(data);
  };

  return (
    <Container justify="center">
      <div className="App">
        <Typography variant="h2">HMAC Generator</Typography>
        <TextBox
          id="textForDecoding"
          name="plainText"
          label="Text for plainText"
          value={plainText}
          rows={10}
          onChange={onChange}
        />
        <TextBox
          id="secretForDecoding"
          name="secret"
          label="Text for secret"
          value={secret}
          onChange={onChange}
        />

        <ButtonGroup variant="contained">
          <Button startIcon={<ArrowDownwardOutlined /> } onClick={hashString}>Encode</Button>
        </ButtonGroup>
        <TextBox
          id="hashedMd5"
          name="hashedMd5"
          label="Text for HMAC_MD5"
          value={hashedMd5}
        />
        <TextBox id="hashedSha1" label="Text for SHA1" value={hashedSha1} />
        <TextBox
          id="hashedSha224"
          label="Text for HMAC_SHA224"
          value={hashedSha224}
        />
        <TextBox
          id="hashedSha256"
          label="Text for HMAC_SHA256"
          value={hashedSha256}
        />
        <TextBox id="hashedSha3" label="Text for SHA3" value={hashedSha3} />
        <TextBox
          id="hashedSha384"
          label="Text for HMAC_SHA384"
          value={hashedSha384}
        />
        <TextBox
          id="hashedSha512"
          label="Text for HMAC_SHA512"
          value={hashedSha512}
        />
      </div>
    </Container>
  );
}

export default HMACGenerator;
